@charset "UTF-8";
.section {
  padding: 100px 0;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  background-color: var(--white); }
  .section__light {
    background-color: #f5f8fa; }
  .section__dark {
    background-color: var(--dark);
    color: var(--white); }
  .section__primary {
    background-image: linear-gradient(to right bottom, #0b5d9e, #0074a9, #0089ae, #2e9db0, #5ab0b2);
    color: var(--white); }
  .section__secondary {
    background-image: linear-gradient(to right bottom, #139b48, #48a84f, #69b558, #86c263, #a2cf6f);
    color: var(--white); }
  .section__border-top {
    border-top: 1px solid rgba(0, 0, 0, 0.15); }

@media only screen and (max-width: 992px) {
  .mobile-align-center {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .mobile-mb-50 {
    margin-bottom: 100px; }
  .flex-reverse-mobile {
    flex-direction: column-reverse !important; } }

@media only screen and (max-width: 520px) {
  .section {
    padding: 30px 0; } }

.section--hero {
  height: 100vh;
  background-position: center;
  background-size: cover;
  color: var(--white);
  padding-top: 170px;
  overflow: hidden; }
  .section--hero .container {
    position: relative;
    z-index: 5; }
  .section--hero .earth-wrapper {
    position: absolute;
    bottom: -250px;
    right: -250px;
    height: 1000px;
    width: 1000px; }
  .section--hero .earth {
    width: 100%; }
  .section--hero .hero-divider {
    position: absolute;
    bottom: -20px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
    .section--hero .hero-divider .divider {
      width: 100%; }
    .section--hero .hero-divider .scroll-down {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      opacity: .5;
      transition: all 200ms ease-in-out; }
      .section--hero .hero-divider .scroll-down span {
        margin-bottom: 5px;
        font-weight: 900; }
      .section--hero .hero-divider .scroll-down .caret {
        animation: MoveUpDown 500ms alternate infinite; }
      .section--hero .hero-divider .scroll-down:hover {
        opacity: 1; }

@-webkit-keyframes MoveUpDown {
  0% {
    transform: translateY(0); }
  100% {
    transform: translateY(5px); } }

@keyframes MoveUpDown {
  0% {
    transform: translateY(0); }
  100% {
    transform: translateY(5px); } }

@media only screen and (max-width: 1200px) {
  .section--hero .hero-divider {
